<template>
    <div class="contain" id="cc" ref='ratings'>
        <div>
            <div class="seller">
                <div class="shop">
                    <div class="star">
                        <p>{{getSellerInfo.name}}</p>
                        <div class="shape">
                            <Star :score="getSellerInfo.score" :size="'middle'"></Star>
                        </div>
                        <span class="t1">(661)</span>
                        <span class="t2">月售{{getSellerInfo.sellCount}}单</span>
                    </div>
                    <div class="collect">
                        <img src="../../../static/imgs/爱心 红.png" alt="">
                        <p>已收藏</p>
                    </div>
                </div>
                <div class="shopinfo">
                    <div class="info1">
                        <p>起送价</p>
                        <span class="info1_t1">{{getSellerInfo.minPrice}}</span>
                        <span class="info1_t2">元</span>
                    </div>
                    <div class="info2">
                        <p>商家配送</p>
                        <span class="info1_t1">{{getSellerInfo.deliveryPrice}}</span>
                        <span class="info1_t2">元</span>
                    </div>
                    <div class="info3">
                        <p>平均配送时间</p>
                        <span class="info1_t1">{{getSellerInfo.deliveryTime}}</span>
                        <span class="info1_t2">分钟</span>
                    </div>
                </div>
                
            </div>

            <!-- 分割线 -->
            <div class="hr1"></div>

            <!-- 公告与活动 -->
            <div class="exercise">
                <p>公告与活动</p>
                <p>{{getSellerInfo.bulletin}}</p>
                <hr style=" height:1px;border:none;border-top:1px solid rgba(7,17,27,0.1)">
                <div class="tabs">
                    <div class="tab_item" v-for="(v,k) in getSellerInfo.supports" :key="k">
                        <img :src="v | imgChoose" alt="">
                        <span>{{v.description}}</span>
                    </div>
                </div>
            </div>

            <!-- 分割线 -->
            <div class="hr1"></div>

            <!-- 商家实景 -->
            <div class="sence">
                <p>商家实景</p>
                <div ref='picwrap'>
                    <div class="pics" ref = 'piclist'>
                        <img :src="v1" alt="" v-for="(v1,k1) in getSellerInfo.pics" :key="k1">
                        
                    </div>
                </div>
                
                
            </div>

            <!-- 分割线 -->
            <div class="hr1"></div>

            <div class="sellerinfo">
                <p>公告与活动</p>
                <hr style=" height:1px;border:none;border-top:1px solid rgba(7,17,27,0.1)">
                <div class="si">
                    <div class="si_item" v-for="(v2,k2) in getSellerInfo.infos" :key="k2">
                        <span>{{v2}}</span>
                    </div>
                    

                </div>
            </div>
        </div>
        

    </div>
</template>

<script>
import { mapGetters } from 'vuex'
import Star from '../star/star'
import BScroll from 'better-scroll'
import imgChoose from '../pic.js'
export default {
    components:{
        Star
    },
    computed : {
        ...mapGetters([
            'getSellerInfo'
        ])
    },
    watch:{
        seller(){
            this._initPicture();
        }
    },
    mounted(){
        this.$nextTick( () => {
            if( !this.sellerSCroll ){
                this.sellerSCroll = new BScroll( document.getElementById('cc'),{
                });
            }else{
                this.sellerSCroll.refresh();
            }
        });
        this._initPicture();
    },
    methods:{
        _initPicture(){
            if( this.getSellerInfo.pics){
                let pw = 240;
                let m = 12;
                let w = ( pw + m ) * this.getSellerInfo.pics.length - m;
                this.$refs.piclist.style.width = w/100 + 'rem';

                if( !this.picBScroll ){
                    this.picBScroll = new BScroll( this.$refs.picwrap ,{
                        scrollX : true ,
                        eventPassthrough : 'vertical'
                    })
                }else{
                    this.picBScroll.refresh();
                }
            }

        }
    }
}
</script>

<style lang="less" scoped>
    .contain{
        position: absolute;
        top: 3.52rem;
        bottom:1rem;
		z-index: -55;
        width: 100%;
        overflow: hidden;
        .seller{
            padding: .36rem;
            .shop{
                height: 1.14rem;
                display: flex;
                justify-content:space-between;
                flex-wrap: nowrap;
                border-bottom: 1px solid rgba(7,17,27,0.1);
                .star{
                    height: .29rem;
                    // border: 1px solid black;
                    line-height: .33rem;
                    width: 4.4rem;
                    .shape{
                        width: 2rem;
                        display: inline-block;
                        vertical-align: center;
                        padding: 0;
                    }
                    p{
                        font-size: .28rem;
                        color: rgb(7,17,27);
                        line-height: .28rem;
                        margin-bottom: .2rem;
                    }
                    img{
                        margin-right: .12rem;
                        width: .29rem;
                        height: .29rem;
                    }
                    .t1{
                        margin-left: .04rem;
                        font-size: .2rem;
                        vertical-align: top;
                        color: rgb(77,85,93);
                    }
                    .t2{
                        margin-left: .24rem;
                        vertical-align: top;
                        font-size: .2rem;
                        color: rgb(77,85,93);
                        line-height: .35rem;
                    }
                }
                .collect{
                    img{
                        width: .48rem;
                        height: .48rem;
                        margin-bottom: .08rem;
                    }
                    p{
                        font-size: .2rem;
                        color: rgb(77,85,93);
                        line-height: .2rem;
                        position: relative;
                        left:-0.05rem;
                    }
                }
            }
            .shopinfo{
                display: flex;
                flex-wrap: nowrap;
                justify-content:space-between;
                padding-top: .36rem;
                // border: 1px solid black;
                .info1{
                    width: 2.14rem;
                    height: 0.9rem;
                    // border: 1px solid black;
                    position: relative;
                    left: -.36rem;
                    // border-right: 1px solid #e6e7e8;
                    text-align: center;
                    p:first-child{
                        font-size: .2rem;
                        color:rgb(147,153,159);
                        line-height: .2rem;
                        margin-bottom: .13rem;
                        margin-top: .08rem;
                    }
                    .info1_t1{
                        font-size: .48rem;
                        font-weight: 200;
                        color:rgb(7,17,27);
                        line-height: .48rem;
                        margin-bottom: .08rem;
                        font-weight: 100;
                    }
                    .info1_t2{
                        font-size: .2rem;
                        font-weight: 200;
                        color:rgb(7,17,27);
                        line-height: .48rem;
                        margin-bottom: .08rem;
                    }

                }
                .info2{
                    width: 2.48rem;
                    height: 0.9rem;
                    border-left: 1px solid #e6e7e8;
                    border-right: 1px solid #e6e7e8;
                    box-sizing: border-box;
                    // position: relative;
                    // left: -.36rem;
                    text-align: center;
                    p:first-child{
                        font-size: .2rem;
                        color:rgb(147,153,159);
                        line-height: .2rem;
                        margin-bottom: .13rem;
                        margin-top: .08rem;
                    }
                    .info1_t1{
                        font-size: .48rem;
                        font-weight: 200;
                        color:rgb(7,17,27);
                        line-height: .48rem;
                        margin-bottom: .08rem;
                        font-weight: 100;
                    }
                    .info1_t2{
                        font-size: .2rem;
                        font-weight: 200;
                        color:rgb(7,17,27);
                        line-height: .48rem;
                        margin-bottom: .08rem;
                    }
                }
                .info3{
                    width: 2.14rem;
                    height: 0.9rem;
                    
                    box-sizing: border-box;
                    position: relative;
                    right: -0.36rem;
                    text-align: center;
                    p:first-child{
                        font-size: .2rem;
                        color:rgb(147,153,159);
                        line-height: .2rem;
                        margin-bottom: .13rem;
                        margin-top: .08rem;
                    }
                    .info1_t1{
                        font-size: .48rem;
                        font-weight: 200;
                        color:rgb(7,17,27);
                        line-height: .48rem;
                        margin-bottom: .08rem;
                        font-weight: 100;
                    }
                    .info1_t2{
                        font-size: .2rem;
                        font-weight: 200;
                        color:rgb(7,17,27);
                        line-height: .48rem;
                        margin-bottom: .08rem;
                    }
                }
            }
            
        }
        .hr1{
            width: 100%;
            height: .32rem;
            background: #f3f5f7;
            border-top:1px solid #e6e7e8;
            border-bottom:1px solid #e6e7e8;
        }
        .exercise{
            padding: .36rem .38rem 0 .36rem;
            p:first-child{
                font-size: .27rem;
                font-weight: 500;
                margin-bottom: .16rem;
            }
            p:nth-child(2){
                font-size: .24rem;
                font-weight: 200;
                color:rgb(240,20,20);
                line-height: .48rem;
                padding: 0 .24rem;
                margin-bottom: .32rem;
            }
            .tabs{
                
                .tab_item{
                    border-bottom: 1px solid #e6e7e8;
                    padding: .32rem 0 .32rem .24rem;
                    height: .32rem;
                    
                    img{
                        width: .32rem;
                        height: .32rem;
                        margin-right: .12rem;
                    }
                    span{
                        font-size: .24rem;
                        font-weight: 200;
                        color:rgb(7,17,27);
                        line-height: .32rem;
                        vertical-align: top;
                        line-height: .32rem;
                    }
                }
            }
        }
        .sence{
            padding: .36rem 0 .36rem .36rem;
            // height: 3rem;
            // border: 1px solid black;
            p{
                font-size: .27rem;
                font-weight: 500;
                margin-bottom: .24rem;
            }
            .pics{
                height: 1.8rem;
                width: 7.2rem;
				white-space: nowrap;
				overflow: hidden;
                img{
                    width: 2.4rem;
                    height: 1.8rem;
                    margin-right: .12rem;
                    display: inline-block;
                    &:last-child{
						margin-right: 0;
					}
                }
            }
            
        }
        .sellerinfo{
            padding: .36rem 0.36rem .36rem .36rem;
            p{
                font-size: .27rem;
                font-weight: 500;
                margin-bottom: .24rem;
            }
            .si{
                .si_item{
                    border-bottom: 1px solid #e6e7e8;
                    padding: .32rem 0 .32rem .24rem;
                    height: .32rem;
                    span{
                        font-size: .24rem;
                        font-weight: 200;
                        color:rgb(7,17,27);
                        line-height: .32rem;
                        vertical-align: top;
                        line-height: .32rem;
                    }
                }
            }
        }
    }
</style>




