<template>
    <div class="contain" id="cc" ref='ratings'>
        <div>
            <div class="scores">
            <div class="score">
               <p>{{getSellerInfo_rating.score}}</p>
               <p>综合评分</p>
               <p>高于周边商家{{getSellerInfo_rating.rankRate}}%</p>
            </div>  
            <div class="star">
                <div class="star-item">
                    <span>服务态度</span>
                    <div class="shape">
                        <Star :score="getSellerInfo_rating.serviceScore" :size="'middle'"></Star>
                    </div>
                    
                    <span class="starscore">{{getSellerInfo_rating.serviceScore}}</span>
                </div>
                <div class="star-item">
                    <span>口味评分</span>
                    <div class="shape">
                        <Star :score="getSellerInfo_rating.foodScore" :size="'middle'"></Star>
                    </div>
                    <span class="starscore">{{getSellerInfo_rating.foodScore}}</span>
                </div>
                <div class="arrivetime">
                    <span>送达时间</span>
                    <span>{{getSellerInfo_rating.deliveryTime}}分钟</span>
                </div>
            </div>
       </div>

       <!-- 间隔部件 -->
       <div class="hr1"></div>
        <!-- 评价部件 -->
        <div class="comments">
            <!-- <div class="evaluation">
                <div class="c1">
                    <span>全部</span>
                    <span>{{rating.length}}</span>
                </div>
                <div class="c2">
                    <span>满意</span>
                    <span>47</span>
                </div>
                <div class="c3">
                    <span>不满意</span>
                    <span>10</span>
                </div>
            </div>
            <div class="nike">
                <p> <img src="../../../static/imgs/选中.png" alt=""> 只看有内容的评价</p>
            </div> -->
            <allokon></allokon>
            <!-- 详细评价 -->
            <div class="usercomments">
                <div class="usercomment" v-for=" (v,k) in selectRatings" :key="k" > 
                    <div class="userhead" >
                        <img :src="v.avatar" alt="">
                    </div>
                    <div class="right">
                        <span v-if="rating[k]">{{v.username}}</span>
                        <span>{{v.rateTime | dateFormat}}</span>
                        <div class="userscore">
                            <div class="shape">
                                <Star :score="v.score" :size="'small'"></Star>
                            </div>
                            <span>{{v.deliveryTime}}分钟送达</span>
                        </div>
                        <div class="usercomment">
                            <p>{{v.text}}</p>
                        </div>
                        <div class="recommend">
                            <img src="../../../static/imgs/好评.png" alt="" v-show="!v.rateType">
                            <img src="../../../static/imgs/差评.png" alt="" v-show="v.rateType">
                            <div class="recommends">
                                <div class="name1" v-for=" (v1,k1) in v.recommend" :key="k1"><p>{{v1}}</p></div>
                            </div>
                        </div>
                    </div>
                </div>  
            </div>
        </div>
        </div>        
    </div>
</template>

<script>
import { mapGetters } from 'vuex'
import Star from '../star/star'
import allokon from '../allokon/allokon'
import BScroll from 'better-scroll'

export default {
    components:{
        Star,
        allokon
    },
    computed : {
        ...mapGetters([
            'getSellerInfo_rating',
            'rating',
            'selectRatings'
        ])
    },
    filters:{
        dateFormat:function(cc){
           var time = new Date(cc);
                var y = time.getFullYear();//年
                var m = time.getMonth() + 1;//月
                var d = time.getDate();//日
                var h = time.getHours();//时
                var mm = time.getMinutes();//分
                var s = time.getSeconds();//秒
                // console.log(y+"-"+m+"-"+d+" "+h+":"+mm+":"+s);
                return y+"-"+m+"-"+d+" "+h+":"+mm;
        }
    },
    methods:{    
        
    },
    mounted(){
        
        this.$nextTick( () => {
            setTimeout(() => {
                this.$store.dispatch('changeTypes',['全部','满意','不满意']);
            }, 30);
            if( !this.sellerSCroll ){
                this.sellerSCroll = new BScroll( document.getElementById('cc'),{
                    click:true
                });
            }else{
                this.sellerSCroll.refresh();
            }
        })

        
    }
    
}
</script>

<style lang="less" scoped>
    .contain{
        position: absolute;
        top: 3.52rem;
        bottom:1rem;
		z-index: -55;
        width: 100%;
        overflow: hidden;
        .scores{
            padding: .36rem .48rem .36rem 0;
            width: 100%;
            height: 2.12rem;
            box-sizing: border-box;
            display: flex;
            flex-wrap: nowrap;
            .score{
                width: 2.75rem;
                border-right: 1px solid #e6e7e8;
                p:first-child{
                    font-size: 0.48rem;
                    color: rgb(255,153,0);
                    line-height: 0.56rem;
                    text-align: center;
                    margin-bottom: .12rem;
                }
                p:nth-child(2){
                    font-size: 0.24rem;
                    color: rgb(7,17,27);
                    line-height: 0.24rem;
                    text-align: center;
                    margin-bottom: .16rem;
                }
                p:nth-child(3){
                    font-size: 0.20rem;
                    color: #93999f;
                    line-height: 0.20rem;
                    text-align: center;
                }

            }
            .star{
                width: 4.74rem;
                padding-left: .48rem;
                .star-item{
                    margin-bottom: .24rem;
                    height: .29rem;
                    span{
                        font-size: 0.24rem;
                        color: rgb(7,17,27);
                        line-height: .29rem;
                        vertical-align: top;
                        margin-right: .24rem;
                    }
                    .shape{
                        width: 2rem;
                        display: inline-block;
                        vertical-align: center;
                        padding: 0;
                    }
                    img{
                        width: .29rem;
                        height: .29rem;
                        vertical-align: center;
                    }
                    .starscore{
                        margin-left: .12rem;
                        font-size: 0.24rem;
                        color: rgb(255,153,0);
                        line-height: .32rem;
                        vertical-align: top;
                    }
                }
                .arrivetime{
                    height: .24rem;
                    span:first-child{
                        font-size: 0.24rem;
                        color: rgb(7,17,27);
                        line-height: .24rem;
                        margin-right: .24rem;
                    }
                    span:nth-child(2){
                        font-size: 0.24rem;
                        color: rgb(147,153,159);
                        line-height: .24rem;
                        font-weight: 200;
                    }
                }
            }
        }
        .hr1{
            width: 100%;
            height: .32rem;
            background: #f3f5f7;
            border-top:1px solid #e6e7e8;
            border-bottom:1px solid #e6e7e8;
        }

        .comments{
            
            .evaluation{
                margin: .36rem .36rem 0 .36rem;
                border-bottom:1px solid #e6e7e8;
                .c1{
                    background-color: #00a0dc;
                    width: 1.2rem;
                    height: .64rem;
                    border-radius: 0.04rem;
                    line-height: .64rem;
                    text-align: center;
                    display: inline-block;
                    margin-right: .16rem;
                    margin-bottom: .36rem;
                    span:first-child{
                        font-size: .22rem;
                        color: white;
                    }
                    span:nth-child(2){
                        font-size: .12rem;
                        color: white;
                        position: relative;
                        top:-0.025rem;
                    }
                }
                .c2{
                    background-color: #ccecf8;
                    width: 1.2rem;
                    height: .64rem;
                    border-radius: 0.04rem;
                    line-height: .64rem;
                    text-align: center;
                    display: inline-block;
                    margin-right: .16rem;
                    span:first-child{
                        font-size: .22rem;
                        color: #4d555d;
                    }
                    span:nth-child(2){
                        font-size: .12rem;
                        color: #4d555d;
                        position: relative;
                        top:-0.025rem;
                    }
                }
                .c3{
                    background-color: #e9ebec;
                    width: 1.2rem;
                    height: .64rem;
                    border-radius: 0.04rem;
                    line-height: .64rem;
                    text-align: center;
                    display: inline-block;
                    margin-right: .16rem;
                    span:first-child{
                        font-size: .22rem;
                        color: #4d555d;
                    }
                    span:nth-child(2){
                        font-size: .12rem;
                        color: #4d555d;
                        position: relative;
                        top:-0.025rem;
                    }
                }
            }
            .nike{
                padding: 0 .36rem 0 .36rem;
                height: .97rem;
                width: 100%;
                border-bottom: 2px solid  #e6e7e8;
                line-height: .97rem;
                p{
                    color: #93999f;
                    font-size: .22rem;
                    img{
                        width: .40rem;
                        height: .4rem;
                        margin-right: .12rem;
                        position: relative;
                        top:.1rem;
                    }
                }
            }
        } 
        .usercomment{
            padding: .36rem;
            display: flex;
            border-bottom: 1px solid #e6e7e8;
            .userhead{
                height: 100%;
                width: .56rem;
                // border: 1px solid black;
                margin-right: .24rem;
                // border-bottom: 1px solid #e6e7e8;
                img{
                    width: .56rem;
                    height: .56rem;
                }  
            }
            .right{
                // border-bottom: 1px solid #e6e7e8;
                >span:first-child{
                    font-size: 0.2rem;
                    color: rgb(7,17,27);
                    line-height: .24rem;
                    font-weight: 200;
                }
                >span:nth-child(2){
                    font-size: 0.2rem;
                    color: rgb(147,153,159);
                    line-height: .24rem;
                    font-weight: 100;
                    position: absolute;
                    right:.36rem;
                }
                .userscore{
                    margin-top: .13rem;
                    .shape{
                        width: 1.6rem;
                        display: inline-block;
                        vertical-align: center;
                        padding: 0;
                    }
                    img{
                        width: .19rem;
                        height: .19rem;
                        margin-right: 0.06rem;
                    }
                    span{
                        font-size: 0.2rem;
                        color: rgb(147,153,159);
                        line-height: .24rem;
                        font-weight: 200;
                    }
                }
                .usercomment{
                    padding: 0;
                    margin-top: .21rem;
                    margin-bottom: .23rem;
                    border-bottom: none;
                    p{
                        font-size: 0.24rem;
                        color: rgb(7,17,27);
                        line-height: .36rem;
                        font-weight: 200;
                    }
                }
            }
        } 
        .recommend{
            display: flex;
            img{
                width: .3rem;
                height: .24rem;
                margin-right: .16rem;
                position: relative;
                top:0.05rem;
            }
            .recommends{
                // border: 1px solid black;
                .name1{
                    width: 1.26rem;
                    height: .32rem;
                    border: 1px solid rgba(7,17,27,0.1);
                    border-radius: 0.02rem;
                    display: inline-block;
                    vertical-align: middle;
                    margin-right: .16rem;
                    margin-bottom: .1rem;
                    // white-space: nowrap;
                    // overflow: hidden;
                    // text-overflow: ellipsis;
                    p{
                        text-align: center;
                        font-size: 0.1rem;
                        color: rgb(147,153,159);
                        line-height: .32rem;
                        font-weight: 200;
                        white-space: nowrap;
                        overflow: hidden;
                        text-overflow: ellipsis;

                    }
                }
            }
            
        }
    }
</style>




