<template>

    <div class="contain">
        
        <div class="bg">
            <img :src="getSellerInfo.avatar" alt="" v-if='getSellerInfo' class="pic1">
         </div>    
        <div class="top">
            <div class="pic">
                <img :src="getSellerInfo.avatar" v-if='getSellerInfo' alt="" class="pic2" @click='showDetail'>
            </div>
            <div class="txt">
                <div>
                    <img src="../../../static/imgs/brand.png" alt="" class="brand">
                    <span v-if='getSellerInfo' class="t1">{{getSellerInfo.name}}</span>
                </div>
                
                <p v-if='getSellerInfo' class="t2">{{getSellerInfo.description}}/{{getSellerInfo.deliveryTime}}分钟送达</p>
                <div class="activty">
                    <img src="../../../static/imgs/0.png" alt="" class="minus">
                    <span v-if='getSellerInfo.supports' class="t3">{{getSellerInfo.supports[0].description}}</span>
                    
                </div>

                <div class="activtyNum" v-if='getSellerInfo'>
                    <p @click='showDetail' v-if='getSellerInfo.supports'>{{getSellerInfo.supports.length}}个 ></p>
                </div>
            </div>   
        </div>

        <div class="bottom" @click='showDetail'>
            <img src="../../../static/imgs/bulletin.png" alt="">
            <p v-if='getSellerInfo' class="bottom_t1">{{getSellerInfo.bulletin}}</p>
            <p class="bottom_t2">></p>
        </div>
        <sellerDetail></sellerDetail>
    </div>

</template>

<script>
    import { mapGetters } from 'vuex'
    import sellerDetail from '../sellerDetail/sellerDetail.vue'
    
    export default {
        components:{
            sellerDetail
        },
        computed : {
            ...mapGetters([
                'getSellerInfo'
            ])
        },
        methods:{
			showDetail(){
				this.$store.dispatch('showDetail');
			}
		}
    }
</script>

<style lang="less" scoped>
.contain{
    width: 100%;
    height: 2.68rem;
    overflow: hidden;
    background: rgba(7,17,27,0.5);
    box-sizing: border-box;
    position: relative;
    >.bg{
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        z-index: -5;
        filter: blur(5px);
        >.pic1{
            width: 100%;
            height: 100%;
        }
    }
    .top{
        box-sizing: border-box;
        padding: 0.48rem 0.24rem 0 0.48rem;
        margin-bottom: .36rem;
        .pic{
            border-radius: 0.04rem;
            width: 1.28rem;
            height: 1.28rem;
            // border: 1px solid black;
            display: inline-block;
            vertical-align: top;
            >.pic2{
                width: 100%;
                height: 100%;
                border-radius: 0.04rem;
            }
        }
        .txt{
            box-sizing: border-box;
            // border: 1px solid black;
            height: 1.28rem;
            width: 5rem;
            display: inline-block;
            margin: 0.04rem 0 0.04rem 0.32rem;
            .brand{
                width: 0.6rem;
                height: 0.36rem;
            }
            .t1{
                font-size: 0.32rem;
                color: rgb(255,255,255);
                font-weight: bold;
                line-height: 0.36rem;
                vertical-align: top;
                margin-left: 0.12rem;
                img{
                    line-height: 0.36rem;
                }
            }
            .t2{
                font-size: 0.24rem;
                color: rgb(255,255,255);
                font-weight: 100;
                line-height: 0.24rem;
                margin-top: 0.16rem;
                margin-bottom: .2rem;
            }
            .activty{
                height: .24rem;
                // border: 1px solid black;
                vertical-align: top;
                .minus{
                    width: .24rem;
                    height: .24rem;
                    margin-right: 0.08rem;
                }
                .t3{
                    font-size: 0.2rem;
                    color: rgb(255,255,255);
                    font-weight: 100;
                    line-height: 0.10rem;
                    position: relative;
                    top: -0.0544rem;
                    
                }
            }
            .activtyNum{
                width: 0.86rem;
                height: 0.48rem;
                line-height: 0.48rem;
                // border: 1px solid black;
                border-radius: 2.2rem;
                float: right;
                background: rgba(0,0,0,0.2);
                position: relative;
                top:-0.365rem;
                >p{
                    text-align:center;
                    font-size: 0.2rem;
                    color: rgb(255,255,255);
                    font-weight: 200;
                }
            }
        }
        
    }
    .bottom{
        height: .56rem;
        width: 100%;
        line-height: .56rem;
        background-color: rgba(7,17,27,0.2);
        // border: 1px solid black;
        font-size: 0;
        box-sizing: border-box;
        vertical-align: center !important;
        img{
            width: .44rem;
            height: .24rem;
            margin-left: .24rem;
            display: inline-block;
            position: relative;
            top:-0.2rem;
            // vertical-align: center !important;
        }
        p{
            font-size: 0.2rem;
            color: rgb(255,255,255);
            font-weight: 100;
            // line-height: .56rem;
            display: inline-block;
            margin-left: .08rem;
            overflow: hidden;
            // border: 1px solid black;
            width: 6.25rem;
            height: .56rem;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            position: relative;
            top:-0.02rem;
        }
        .bottom_t2{
            display: inline-block;
            width: 0.2rem;
            font-size: 0.2rem;
            color: rgb(255,255,255);
            font-weight: 100;
            // position: relative;
            // top:-0.225rem;
        }

    }  

}
</style>





