<template>
    <div class="contain" v-show="chooseFlag">
        <div class="transparent" @click.stop.prevent="closechoosed"></div>

        <div class="detail">
            <div class="title">
                <span @click="closechoosed">购物车</span>
                <span @click="clearchoosed">清空</span>
            </div>
            <div class="sc">
                <div class="food" v-for="(v,k) in choose" :key="k" v-show="v.count">
                    <span>{{v.name}}</span>
                    <span>¥{{v.price}}</span>
                    <addsub :price = 'v' class="c2"></addsub>
                </div>
            </div>
            
        </div>

    </div>
</template>

<script>
import { mapGetters } from 'vuex'
import addsub from '../addsub/addsub'
export default {
    components : {
        addsub
    },
    computed : {
        ...mapGetters([
            'choose',
            'chooseFlag'
        ])
    },
    methods:{
        closechoosed(){
            // console.log(5)
            this.$store.dispatch('closechoosed')
        },
        clearchoosed(){
            this.choose.forEach( (food) => {
                food.count = 0;
            })
            this.$store.dispatch('clearchoosed')
        }
    }
}
</script>

<style lang="less" scoped>
 /* for Chrome */
.detail::-webkit-scrollbar {
    display: none;
}
    .contain{
        display: flex;
        flex-wrap: wrap ;
        border: 1px solid black;
        box-sizing: border-box;
        width: 90.6%;
        height: 12.18rem;
        display:flex;
		flex-direction:column;
        .transparent{
            background: rgba(7, 17, 27, 0.6);
            // filter:blur(10px);
            // z-index: -1;
            width: 100%;
            flex:1;
        }
        .detail{
            max-height: 6rem;
            width: 100%;
            overflow-y: scroll;

            .title{
                height: .8rem;
                background: #f3f5f7;
                
            }
            span:first-child{
                font-size: .28rem;
                font-weight: 200;
                color: rgb(7, 17, 27);
                line-height: .8rem;
                margin-left: .36rem;
                display: inline-block;
                width: 1.6rem;
            }
            span:last-child{
                font-size: .24rem;
                color: rgb(0 , 160, 220);
                line-height: .8rem;
                margin-left: 4.5rem;
            }
        }
        .sc{
            .food{
                padding: .24rem .36rem;
                border-bottom: 1px solid rgba(7,17,27,0.1);
                background : white;
                height: .96rem;
                box-sizing: border-box;
                span:first-child{
                    margin: 0;
                    font-size: .28rem;
                    color: rgb(7, 17, 27);
                    line-height: .48rem;
                    width: 3rem;
                }
                span:nth-child(2){
                    font-size: .28rem;
                    font-weight: 700;
                    color: red;
                    line-height: .48rem;
                    margin-left: 1.4rem;

                }
                .c2{
                    vertical-align: top;
                }
            }
        }
        
    }
</style>





