import Vue from 'vue'

Vue.filter('imgChoose',function(value){
    if(value.type==0){
        return './static/imgs/0.png'
    }
    if(value.type==1){
        return './static/imgs/1.png'
    }
    if(value.type==2){
        return './static/imgs/2.png'
    }
    if(value.type==3){
        return './static/imgs/3.png'
    }
    if(value.type==4){
        return './static/imgs/4.png'
    }
})


