<template>
    <transition enter-active-class = 'animated fadeInDown' leave-active-class = 'animated fadeOutLeft'>
        <div class="contain" v-show='flag1' id="cc" ref='ratings'>
            <div>
                <div class="pic">
                    <span  @click="shutdown1" > × </span>
                    <img :src="food1.image" alt="">
                </div>
                <div class="info">
                    <p>{{food1.name}}</p>
                    <span>月售{{food1.sellCount}}份</span>
                    <div class="price">
                        <span>￥</span>
                        <span>{{food1.price}}</span>
                        <span class="through" v-if="food1.oldPrice">￥{{food1.oldPrice}}</span>
                        <div class="btn">
                            <p v-show="carflag" @click="carflagshow">加入购物车</p>
                            <addsub :price="food1" v-show="!carflag"></addsub>
                        </div>
                    </div>
                </div>
                <!-- 间隔部件 -->
            <div class="hr1"></div>

            <div class="introduce">
                <p>商品介绍</p>
                <p>{{food1.info}}</p>
            </div>

            <!-- 间隔部件 -->
            <div class="hr1"></div>

                <!-- 评价部件 -->
            <div class="comments">

                    <allokon></allokon>

                    <!-- 详细评价 -->
                    <div class="usercomment">
                        <div v-for="(v,k) in selectRatings" :key="k">
                            <div class="user">
                                <span>{{v.rateTime}}</span>
                                <span>{{v.username}}</span>
                                <img src="../../../static/imgs/timg.jpg" alt="">
                            </div>
                            <div class="comment">
                                <img src="../../../static/imgs/好评.png" alt="">
                                <span>{{v.text}}</span>
                            </div>
                        </div>
                        
                    </div>
                </div>
            </div>   
        </div>
    </transition>
</template>

<script>
import { mapGetters } from 'vuex'
import animate from 'animate.css'
import BScroll from 'better-scroll'
import addsub from '../addsub/addsub'
import allokon from '../allokon/allokon'
export default {
    components:{
        addsub,
        allokon
    },
    computed:{
        ...mapGetters([
            'food1',
            'flag1',
            'carflag',
            'selectRatings'
        ])
    },
    methods:{
        shutdown1(){
            this.$store.dispatch('shutdown');
        },
        carflagshow(){
            this.$store.dispatch('carflagshow');
        }
    },
    watch:{
        food1(){
            // console.log(this.food1);
            this.$store.dispatch('changeArrs',this.food1);
        }
    },
    mounted(){
        this.$nextTick( () => {
            setTimeout(() => {
                this.$store.dispatch('changeTypes',['全部','推荐','吐槽']);
                // this.$store.dispatch('changeArrs',this.food1);
                // console.log(this.food1)
            }, 300);
            if( !this.sellerSCroll ){
                this.sellerSCroll = new BScroll( document.getElementById('cc'),{
                    click : true
                });
            }else{
                this.sellerSCroll.refresh();
            }
        })
        
    }
}
</script>

<style lang="less" scoped>
    .contain{
        background: white;
        position: fixed;
        left: 0;
        top: 0;
        bottom: 1rem;
        width: 100%;
        // height: 100%;
        display: block;
        
        .pic{
            width: 100%;
            height: 7.5rem;
            span{
                
                position: absolute;
                top:0.3rem;
                left:0.3rem;
                font-size: .48rem;
                color: rgba(7, 17, 27, 0.5)
            }
            img{
                width: 100%;
                height: 100%;
            }
        }
        .info{
            padding: .36rem;
            >p{
                font-size: .28rem;
                font-weight: 700;
                color: rbg(7,17,27);
                line-height: .28rem;
                margin-bottom: .16rem;
            }
            >span{
                font-size: .2rem;
                color: rgb(147,153,159);
                line-height: .2rem;
                margin-bottom: .36rem;
            }
            .price{
                height: .48rem;
                span:first-child{
                    font-size: .2rem;
                    font-weight: 700;
                    color: red;
                    line-height: .48rem;
                }
                span:nth-child(2){
                    font-size: .28rem;
                    font-weight: 700;
                    color: red;
                    line-height: .48rem;
                }
                .through{
                    font-size: .2rem;
                    font-weight: 700;
                    color: rgb(147,153,159);
                    line-height: .48rem;
                    vertical-align: top;
                    text-decoration: line-through;
                }
                .btn{
                    display: inline-block;
                    width: 1.48rem;
                    height: .48rem;
                    
                    // background-color: rgb(0,160,220); 
                    line-height: .48rem;
                    text-align: center;
                    position: absolute;
                    right: .36rem;
                    p{
                        font-size: .2rem;
                        color: white;
                        background-color: rgb(0,160,220); 
                        border-radius: .24rem;
                    }
                }
            }
        }
        .hr1{
            width: 100%;
            height: .32rem;
            background: #f3f5f7;
            border-top:1px solid #e6e7e8;
            border-bottom:1px solid #e6e7e8;
        }
        .introduce{
            padding: .36rem;
            p:first-child{
                font-size: .27rem;
                font-weight: 500;
                margin-bottom: .16rem;
            }
            p:nth-child(2){
                font-size: .24rem;
                font-weight: 200;
                color:rgb(77,85,93);
                line-height: .48rem;
                padding: 0 .24rem;
            }
        }
        .comments{
            
            .evaluation{
                margin: .36rem .36rem 0 .36rem;
                border-bottom:1px solid #e6e7e8;
                box-sizing: border-box;
                .c1{
                    background-color: #00a0dc;
                    width: 1.2rem;
                    height: .64rem;
                    border-radius: 0.04rem;
                    line-height: .64rem;
                    text-align: center;
                    display: inline-block;
                    margin-right: .16rem;
                    margin-bottom: .36rem;
                    span:first-child{
                        font-size: .22rem;
                        color: white;
                    }
                    span:nth-child(2){
                        font-size: .12rem;
                        color: white;
                        position: relative;
                        top:-0.025rem;
                    }
                }
                .c2{
                    background-color: #ccecf8;
                    width: 1.2rem;
                    height: .64rem;
                    border-radius: 0.04rem;
                    line-height: .64rem;
                    text-align: center;
                    display: inline-block;
                    margin-right: .16rem;
                    span:first-child{
                        font-size: .22rem;
                        color: #4d555d;
                    }
                    span:nth-child(2){
                        font-size: .12rem;
                        color: #4d555d;
                        position: relative;
                        top:-0.025rem;
                    }
                }
                .c3{
                    background-color: #e9ebec;
                    width: 1.2rem;
                    height: .64rem;
                    border-radius: 0.04rem;
                    line-height: .64rem;
                    text-align: center;
                    display: inline-block;
                    margin-right: .16rem;
                    span:first-child{
                        font-size: .22rem;
                        color: #4d555d;
                    }
                    span:nth-child(2){
                        font-size: .12rem;
                        color: #4d555d;
                        position: relative;
                        top:-0.025rem;
                    }
                }
            }
            .nike{
                padding: 0 .36rem 0 .36rem;
                height: .97rem;
                width: 100%;
                border-bottom: 2px solid  #e6e7e8;
                line-height: .97rem;
                p{
                    color: #93999f;
                    font-size: .22rem;
                    img{
                        width: .40rem;
                        height: .4rem;
                        margin-right: .12rem;
                        position: relative;
                        top:.1rem;
                    }
                }
            }
        }
        .usercomment{
            padding: .32rem .36rem;

            .user{
                padding-top: .36rem;
                height: .24rem;
                line-height: .24rem;
                span:first-child{
                    font-size: .2rem;
                    color: rgb(147,153,159);
                    line-height: .24rem;
                    vertical-align: top;
                }
                span:nth-child(2){
                    font-size: .2rem;
                    color: rgb(147,153,159);
                    line-height: .24rem;
                    margin-right: .12rem;
                    vertical-align: top;
                    position: absolute;
                    right:.72rem;
                }
                img{
                    width: .24rem;
                    height: .24rem;
                    border-radius: .12rem;
                    position: absolute;
                    right:.36rem;
                }
            }
            .comment{
                border-bottom: 1px solid grey;
                margin-top: .12rem;
                padding-bottom: .36rem;
                >img{
                   width: .24rem;
                   margin-right: .08rem;
                   
                }
            }
        } 
    }
</style>





