import axios from 'axios';

const state = {
    good:[],
    flag1:false,
    food:{},
    choose:[],
    chooseFlag:false,
    carflag:true

}

const getters = {
    //返回food数据
    food (state) {
        return state.good;
    },
    flag1 (state) {
        return state.flag1;
    },
    food1 (state) {
        return state.food;
    },
    choose(state){
        return state.choose;
    },
    chooseFlag(state){
        return state.chooseFlag;
    },
    carflag(state){
        return state.carflag;
    }

}

const actions = {
    //获取seller数据
    getGoods( {commit} ){
        //发送ajax请求 需要配代理
        axios.get('data.json').then( res => {
            // console.log(res.data.seller);
            commit( 'getGoods' , res.data.goods);
        })
    },
    showFoodDetail( { commit } , food ){
        // console.log(1111);
		commit( 'showFoodDetail' ,food );
    },
    shutdown( { commit } ){
        // console.log(1111);
		commit( 'shutdown'  );
    },
    choosed( { commit } , choose ){
        // console.log(1111);
		commit( 'choosed' ,choose );
    },
     // 关闭选择食品详情;
    closechoosed( { commit } ){
        // console.log(1111);
		commit( 'closechoosed' );
    },
    clearchoosed( { commit } ){
        // console.log(1111);
		commit( 'clearchoosed' );
    },
        // 选择食品详情的状态;
    carflagshow( { commit } ){
        // console.log(1111);
		commit( 'carflagshow' );
    }

}

const mutations = {
    //获取goods数据
    getGoods (state,data){
        state.good = data;
    },
    showFoodDetail (state,food){
        state.flag1 = true;
        state.food = food;
        
        // console.log(state.flag1 );
        // console.log(state.food);
    },
    shutdown(state){
        state.flag1 = false;
        // console.log(state.flag1)
    },
    choosed( state , choose){
        // console.log(2);
        state.choose = choose;
        state.chooseFlag = true;
    },
    closechoosed(state){
        // console.log(2);
        state.chooseFlag = false;
    },
    clearchoosed(state){
        // console.log(state.choose.length);
        state.choose = [];
    },
    carflagshow(state){
        // console.log(state.choose.length);
        state.carflag = false;
    }
  
}

export default {
    state,
	getters,
	mutations,
	actions
}



