import axios from 'axios';

const state = {
    //定义seller属性为对象
    seller : {

    },
    //定义rating属性为数组
    rating : [],
    //默认选择全部
    selectType : 2,
    //选择出来的用户评价
    selectRatings : [],
    //初始化全部推荐不推荐
    types:['满意','不满意','全部'],
    onlyContent:false,
    //只看内容评价一开始默认ture
    defaultflag:true
}

const getters = {
    //返回seller数据
    getSellerInfo_rating (state) {
        return state.seller;
    },
    //返回评价数据
    rating ( state ) {
        return state.rating;
    },
	selectType( state ){
		return state.selectType;
    },
    selectRatings( state ){
		return state.selectRatings;
    },
    types( state ){
		return state.types;
    },
    onlyContent( state ){
		return state.onlyContent;
    },
    defaultflag( state ){
		return state.defaultflag;
    }
}

const actions = {
    //获取seller数据
    getSeller( {commit} ){
        //发送ajax请求 需要配代理
        axios.get('data.json').then( res => {
            // console.log(res.data.seller);
            commit( 'getSeller' , res.data.seller);
        })
    },
    getRatings( {commit} ){
        //发送ajax请求 需要配代理
        axios.get('/api/data.json').then( res => {
            // console.log(res.data.ratings);
            commit( 'getRatings' , res.data.ratings);
        })
    },
    // 点击筛选内容
	select( {commit} ,type){
		commit( 'select',type);
    },
    // 初始化全部推荐不推荐
    changeTypes( {commit} ,name){
		commit( 'changeTypes',name);
    },
    // 查看有评价的评论
    toggle({commit}){
		commit('toggle');
    },
    changeArrs( {commit} ,choosedfoodcomm){
		commit( 'changeArrs',choosedfoodcomm);
    }
}

const mutations = {
    //获取seller数据
    getSeller (state,data){
        state.seller = data;
    },
    getRatings (state,data){
        state.rating = data;
        // state.rating = this.state.goods.food.ra
    },
    select( state , data){
        state.selectType = data;
        state.selectRatings = state.rating.filter((rating) => {
            // console.log(111)
            if( state.selectType === 2 ){
                return true;
            }else{
                return state.selectType===rating.rateType
            }
        })
        if(state.defaultflag==false){
            state.defaultflag = true;
        }
        
    },
    // 初始化全部推荐不推荐
    changeTypes(state,name){
        state.types = name;
        state.selectType = 2;
        //初始评价数据给全部按钮
        // console.log(state.rating)
		state.selectRatings = state.rating;
    },
    // 查看有评价的评论
    toggle(state){
		state.onlyContent = !state.onlyContent;
		state.selectRatings = state.rating.filter( rating =>{
			//   如果  我想看内容   但是  又没有内容  
			if( state.onlyContent && !rating.text ){
				return false;
			}else if( state.selectType === 2 ){
				return true;
			}else{
				//          0 // 1         0   //  1 
				return rating.rateType == state.selectType;
			}
        }),
        state.defaultflag = !state.defaultflag;
    },
    changeArrs(state,choosedfoodcomm){
        state.rating = choosedfoodcomm.ratings
    }
}

export default {
    state,
	getters,
	mutations,
	actions
}



