<template>
	<div class="star" :class='starType'>
		<span v-for='(itemClass,k) in itemClasses' :class='itemClass' class="star-item" :key="k"></span>
	<!-- 3.6 -->

	<!-- 3    3个完整的星星--> 
	<!-- 0.6  1个一半的星星-->
	<!-- 1    一个灰色的星星 -->

	</div>
</template>

<script>
	
	const LENGTH = 5;
	const ON   = 'on';
	const OFF  = 'off';
	const HALF = 'half'

	export default{
		props : [
			'score',
			'size'
		],
		computed : {
			starType(){
				return 'star-'+this.size;
			},
			itemClasses(){
				// 定义空数组
				let result = [];
				// 取整 3.6 this.scroe
				let integer = Math.floor( this.score ); //3
				let decimal = this.score - integer;		//0.6

				for(let i =0 ; i < integer ;i++){
					result.push( ON );
					// result[ON,ON,ON]
				}
				if( decimal > 0.5 ){
					result.push( HALF );
					// result[ON,ON,ON,HALF]
				}
				while( result.length < LENGTH ){
					result.push( OFF );
					// result[ON,ON,ON,HALF,OFF]
				}
				return result;
			}
		}

	}
</script>

<style scoped lang='less'>
	.star{
		display: flex;
		padding: 0 !important;
		&.star-small{
			padding: 0;
			.star-item{
				display: inline-block;
				width: .19rem;
				height: .19rem;
				margin-right: 0.1rem;
				&.on{
					background-image: url('../../../static/imgs/star_on.png');
					background-size: .19rem .19rem;
					background-repeat: no-repeat;
				}
				&.half{
					background-image: url('../../../static/imgs/star_half.png');
					background-size: .19rem .19rem;
					background-repeat: no-repeat;
				}
				&.off{
					background-image: url('../../../static/imgs/star_off.png');
					background-size: .19rem .19rem;
					background-repeat: no-repeat;
				}
			}
        }
        &.star-middle{
			padding: 0;
			.star-item{
				display: inline-block;
				width: .28rem;
				height: .28rem;
				margin-right: 0.1rem;
				&.on{
					background-image: url('../../../static/imgs/star_on.png');
					background-size: .28rem .28rem;
					background-repeat: no-repeat;
				}
				&.half{
					background-image: url('../../../static/imgs/star_half.png');
					background-size: .28rem .28rem;
					background-repeat: no-repeat;
				}
				&.off{
					background-image: url('../../../static/imgs/star_off.png');
					background-size: .28rem .28rem;
					background-repeat: no-repeat;
				}
			}
        }
        &.star-large{
			padding: 0;
			.star-item{
				display: inline-block;
				width: .38rem;
				height: .38rem;
				margin-right: 0.4rem;
				&.on{
					background-image: url('../../../static/imgs/star_on.png');
					background-size: .38rem .38rem;
					background-repeat: no-repeat;
				}
				&.half{
					background-image: url('../../../static/imgs/star_half.png');
					background-size: .38rem .38rem;
					background-repeat: no-repeat;
				}
				&.off{
					background-image: url('../../../static/imgs/star_off.png');
					background-size: .38rem .38rem;
					background-repeat: no-repeat;
				}
				&:last-child{
					margin-right: 0;
				}
			}
		}
		
	}
</style>


