<template>
    <div class="contain">
       <div class="left-bar" ref='kindwrap'>
           <div>
               <div class="menu-item"  v-for="(v,k) in food" :key="k"  @click='selectMenu(k)' :class='{"current" : currentIndex == k }'>
                    <p>{{v.name}}</p>
                </div>
           </div>
           
           
       </div>
       <div class="right-bar" ref='foodwrap'>
           <div>
               <div class="food-item food-list-hook" v-for="(v1,k1) in food" :key="k1">
                <div class="title">
                    <p>{{v1.name}}</p>
                    </div>
                    <div v-for="(v2,k2) in v1.foods" :key="k2" class="kuang">
                        <div class="hotfood">
                            <img :src="v2.image" alt="" @click='showFoodDetail(v2)'>
                            <div class="fooddetail">
                                <p>{{v2.name}}</p>
                                <p>{{v2.description}}</p>
                                <p>月售{{v2.sellCount}}份     好评率{{v2.rating}}%</p>
                            </div>
                        </div>
                        <div class="price">
                            <span>¥</span>
                            <span>{{v2.price}}</span>
                            <!-- <span class="through" v-if="v2.oldPrice">¥{{v2.oldPrice}}</span> -->
                            <span class="through">¥18</span>
                            <addsub :price='v2' class="add"></addsub>
                        </div>
                    </div>
                </div>
           </div>

       </div>
        <fooddetail></fooddetail>
    </div>
</template>

<script>
import Star from '../star/star'
import BScroll from 'better-scroll'
import fooddetail from '../fooddetail/fooddetail'
import addsub from '../addsub/addsub'
import { mapGetters }  from 'vuex'
export default {
    components:{
        fooddetail,
        addsub
    },
    computed : {
        ...mapGetters([
            'food'
        ]),
        // 计算当前位置在哪一区间内 然后返回当前i
        currentIndex(){
            for( let i = 0 ; i < this.heights.length ; i++){
                let height1	= this.heights[i];
                let height2	= this.heights[i+1];
                if( this.scrollY >= height1 && this.scrollY < height2){
                    return i;
                }
            }
            return 0;
        }
    },
    data(){
        return {
            heights:[],
            scrollY : ''
        }
    },
    mounted(){
        this.$nextTick( () => {
            if( !this.foodwrapBScroll ){
                this.foodwrapBScroll = new BScroll( this.$refs.foodwrap,{
                    click : true,
                    probeType : 3
                })
            }else{
                this.foodwrapBScroll.refresh();
            }
            
        });
        this.$nextTick( () => {
            if( !this.kindwrapBScroll ){
                this.kindwrapBScroll = new BScroll( this.$refs.kindwrap,{
                    click : true,
                    
                })
            }else{
                this.foodwrapBScroll.refresh();
            }
            setTimeout(() => {
                this.initBScroll();
			    this.calcHeight();
            }, 10);
            
        })
    },
    methods:{
        showFoodDetail( f ){
            this.$store.dispatch('showFoodDetail',f);
        },
        initBScroll(){
            this.foodwrapBScroll.on('scroll', (pos) => {
                // console.log(pos);
                this.scrollY = Math.abs( Math.round( pos.y))
                // console.log(this.scrollY);
            }) 
        },
        calcHeight(){
            let foodList = document.getElementsByClassName('food-list-hook');
            let height = 0 ;
            this.heights.push( height );
            for(let i = 0 ; i < foodList.length ; i++){
                //获取每一个li（区块）的高度
                height += foodList[i].clientHeight;
                this.heights.push( height );
            }
            // [0, 599, 684, 769, 968, 1110, 1252, 1451, 1765, 2136]
            // console.log(this.heights);

        },
        // 左侧点击事件
        selectMenu(i){
            // console.log(i)
            let foodList = document.getElementsByClassName('food-list-hook');
            this.foodwrapBScroll.scrollToElement(foodList[i],300);
        }
    }
}
</script>

<style lang="less" scoped>
    .contain{
        position: absolute;
		display: flex;
		overflow: hidden;
		width: 100%;
		top:3.5rem;
        bottom: 1rem;
        .left-bar{
            flex: 0 0 1.6rem;
            width: 1.6rem;
            .menu-item{
                width: 1.12rem;
				height: 1.08rem;
				font-size: 0.24rem;
				background: #f3f5f7;
				color:rgb(7,17,27);
				padding: 0 0.24rem;
				font-weight: 200;
				line-height: 0.28rem;
                display: table;
                &.current{
					background: white;
				}
                >p{
                    width: 1.12rem;
					display: table-cell;
					vertical-align: middle;
					border-bottom: 1px solid rgba(7,17,27,0.1);
                }
            }
        }
        .right-bar{
            flex: 1;
            width: 100%;
            .food-item{
                .title{
                    height: .52rem;
                    background-color: #f3f5f7;
                    border-left: 4px solid #d9dde1;
                    line-height: .52rem;
                    padding-left: .24rem;
                    p{
                        font-size: .23rem;
                        color: #93999f;
                        font-weight: 500;
                    }
                }
                .kuang{
                    margin: .36rem .36rem 0 .36rem;
                    border-bottom: 1px solid rgba(7,17,27,0.1);
                    .hotfood{
                        display: flex;
                        >img{
                            width: 1.15rem;
                            height: 1.15rem;
                            margin-right: .2rem;
                        }
                        .fooddetail{
                            p:first-child{
                                padding-top: .04rem;
                                font-size: .28rem;
                                color: rgb(7,17,27);
                                line-height:.28rem;
                                padding-bottom: .16rem;
                            }
                            p:nth-child(2){
                                font-size: .2rem;
                                color: rgb(147,153,159);
                                line-height:.2rem;
                                padding-bottom: .16rem;
                            }
                            p:last-child{
                                font-size: .2rem;
                                color: rgb(147,153,159);
                                line-height:.2rem;
                            }
                        }
                    }
                    .price{
                        // border: 1px solid black;
                        width: 3.85rem;
                        position: relative;
                        right: -1.35rem;
                        top:-0.05rem;
                        box-sizing: border-box;
                        span:first-child{
                            font-size: .20rem;
                            color: #f01414;
                            font-weight: 700;
                            line-height:.48rem;
                        }
                        span:nth-child(2){
                            font-size: .28rem;
                            color: #f01414;
                            font-weight: 700;
                            line-height:.48rem;
                            margin-right: .16rem;
                        }
                        .through{
                            font-size: .20rem;
                            color: rgb(147,153,159);
                            font-weight: 700;
                            line-height:.48rem;
                            text-decoration: line-through
                        }
                        .add{
                            width: 1.2rem;
                            height: .4rem;
                            vertical-align: top;
                            position: absolute;
                            top:-0.05rem;
                            right: 0;
                        }
                    }
                }
            }
        }
    }
</style>





