<template>
    <div>
        <div class="evaluation">
            <div class="c1" @click='select(2)' :class="{'active' : selectType === 2 }">
                <span>{{types[0]}}</span>
                <span>{{rating.length}}</span>
            </div>
            <div class="c2" @click='select(0)' :class="{'active' : selectType === 0 }">
                <span>{{types[1]}}</span>
                <span>{{okCount}}</span>
            </div>
            <div class="c3" @click='select(1)' :class="{'active' : selectType === 1 }">
                <span>{{types[2]}}</span>
                <span>{{nookCount}}</span>
            </div>
        </div>
        <div class="nike" :class='{"light" : onlyContent}' @click='toggle'>
            <p> <img src="../../../static/imgs/选中.png" alt="" v-show="defaultflag"> <img src="../../../static/imgs/选中 (1).png" alt="" v-show="!defaultflag"><span>只看有内容的评价</span></p>
        </div>
    </div>
    
</template>

<script>
import { mapGetters } from "vuex"
export default {
    // data(){
    //     return{
    //         flag:true
    //     }
    // },
    computed:{
        ...mapGetters([
            'rating',
            'selectType',
            'types',
            'onlyContent',
            'defaultflag'
        ]),
        okCount(){
            return this.rating.filter( rating => {
                return rating.rateType === 0;
            }).length
        },
        nookCount(){
            return this.rating.filter( rating => {
                return rating.rateType === 1;
            }).length
        }
    },
    methods:{
        select(type){
            this.$store.dispatch('select',type);
        },
        toggle(){
            // this.flag = !this.flag;
            this.$store.dispatch('toggle');
        }
    },
    mounted(){
        
    }
}
</script>

<style lang="less" scoped>
.evaluation{
    margin: .36rem .36rem 0 .36rem;
    border-bottom:1px solid #e6e7e8;
    .c1{
        background-color: #00a0dc;
        width: 1.2rem;
        height: .64rem;
        border-radius: 0.04rem;
        line-height: .64rem;
        text-align: center;
        display: inline-block;
        margin-right: .16rem;
        margin-bottom: .36rem;
        box-sizing: border-box;
        &.active{
            border: 1px solid grey;
        }
        span:first-child{
            font-size: .22rem;
            color: white;
        }
        span:nth-child(2){
            font-size: .12rem;
            color: white;
            position: relative;
            top:-0.025rem;
        }
    }
    .c2{
        background-color: #ccecf8;
        width: 1.2rem;
        height: .64rem;
        border-radius: 0.04rem;
        line-height: .64rem;
        text-align: center;
        display: inline-block;
        margin-right: .16rem;
        &.active{
            border: 1px solid grey;
        }
        span:first-child{
            font-size: .22rem;
            color: #4d555d;
        }
        span:nth-child(2){
            font-size: .12rem;
            color: #4d555d;
            position: relative;
            top:-0.025rem;
        }
    }
    .c3{
        background-color: #e9ebec;
        width: 1.2rem;
        height: .64rem;
        border-radius: 0.04rem;
        line-height: .64rem;
        text-align: center;
        display: inline-block;
        margin-right: .16rem;
        &.active{
            border: 1px solid grey;
        }
        span:first-child{
            font-size: .22rem;
            color: #4d555d;
        }
        span:nth-child(2){
            font-size: .12rem;
            color: #4d555d;
            position: relative;
            top:-0.025rem;
        }
    }
}
.nike{
    padding: 0 .36rem 0 .36rem;
    height: .97rem;
    width: 100%;
    border-bottom: 2px solid  #e6e7e8;
    line-height: .97rem;
    p{
        color: #93999f;
        font-size: .22rem;
        img{
            width: .40rem;
            height: .4rem;
            margin-right: .12rem;
            position: relative;
            top:.1rem;
        }
        span{
            position: absolute;
        }
    }
}
</style>





