import axios from 'axios';


const state = {
    //定义seller属性为对象
    seller : {

    },
    flag:false
}

const getters = {
    //返回seller数据
    getSellerInfo (state) {
        return state.seller;
    },
    flag ( state ) {
        return state.flag;
    }
}

const actions = {
    //获取seller数据
  getSeller( {commit} ){
    //发送ajax请求 需要配代理
      axios.get('data.json').then( res => {
        //   console.log(res.data.seller);
          commit( 'getSeller' , res.data.seller);
      })
    },
    //获取seller数据
    // getSeller2( {commit} ){
    // //发送ajax请求 需要配代理
    //   axios.get('/api/data.json').then( res => {
    //       console.log(res.data.seller2);
    //       commit( 'getSeller2' , res.data.seller2);
    //   })
    // },
        //获取商品详情数据
    showDetail( {commit} ){
        commit('showDetail')
    },
        //取消商家行情
    noShowDetail ( {commit} ){
        commit('noShowDetail')
    }
}

const mutations = {
    //获取seller数据
    getSeller (state,data){
        state.seller = data;
    },
    //获取seller数据
    // getSeller2 (state,data){
    //     state.seller2 = data;
    // },
    //获取商品详情数据
    showDetail( state ){
        state.flag = true;
    },
    //取消商家行情
    noShowDetail( state ){
        state.flag = false;
    }
}

export default {
    state,
	getters,
	mutations,
	actions
}



