var LENGTH = 5;
var ON = 'on';
var OFF = 'off';
var HALF = 'half';

/* harmony default export */ var star = ({
	props: ['score', 'size'],
	computed: {
		starType: function starType() {
			return 'star-' + this.size;
		},
		itemClasses: function itemClasses() {
			// 定义空数组
			var result = [];
			// 取整 3.6 this.scroe
			var integer = Math.floor(this.score); //3
			var decimal = this.score - integer; //0.6

			for (var i = 0; i < integer; i++) {
				result.push(ON);
				// result[ON,ON,ON]
			}
			if (decimal > 0.5) {
				result.push(HALF);
				// result[ON,ON,ON,HALF]
			}
			while (result.length < LENGTH) {
				result.push(OFF);
				// result[ON,ON,ON,HALF,OFF]
			}
			return result;
		}
	}

});
