<template>
    <div>
        <choosedfood class="c1"></choosedfood>
        <div class="foodcart">
            <!-- 购物车图片 -->
            <div class="imgs-bg">
                <div style="color:white;" class="count">{{selectFoods.length}}</div>
                <div class="imgs">
                    <img src="../../../static/imgs/shopping.png" alt="" @click="choosed(selectFoods)"> 
                </div>
            </div>
            <!-- 消费 -->
            <div class="consume">
                <p>￥{{totalPrice}}</p>
            </div>
            <!-- 配送费 -->
            <div class="dispatching">
                <p>另需配送费</p>
                <span>￥4元</span>
            </div>
            <!-- 起送费 -->
            <div class="Send">
                <p>{{payDesc}}</p>
            </div>
        </div>
    </div>
    

</template>

<script>
    import { mapGetters } from 'vuex'
    import choosedfood from '../choosedfood/choosedfood.vue'
    export default {
        components:{
            choosedfood
        },
        computed : {
            ...mapGetters([
                'getSellerInfo',
                'food'
            ]),
            selectFoods(){
                let foods = [];
                
                this.food.forEach(good => {
                    good.foods.forEach(food => {
                        if(food.count>0){
                            foods.push(food)
                        }
                    })
                });
                return foods;
            },
            totalPrice(){
                let total = 0;
                // 对上面的计算属性进行遍历（就是对数组遍历，里面的每一个对象都有count）
                this.selectFoods.forEach( food => {
                    total += food.count * food.price;
                });
                return total;
            },
            totalCount(){
                let count = 0 ;
                this.selectFoods.forEach( food => {
                    count += food.count;
                });
                return count;
            },
            payDesc(){
                if( this.totalPrice === 0){
                    return `￥${this.getSellerInfo.minPrice}元起送`;
                }else if( this.totalPrice < this.getSellerInfo.minPrice ){
                    return `差￥${this.getSellerInfo.minPrice - this.totalPrice}元起送`
                }else{
                    return "去结算";
                }
            }

        },
        methods : {
            choosed( choose ){
                this.$store.dispatch('choosed',choose)
            }
        }

    }
</script>

<style lang="less" scoped>
    .c1{
        z-index: -1;
    }
    .foodcart{
        z-index: 10;
        width: 91%;
        height: 1.16rem;
        background: #444;
        display: flex;
        background-color: #141d27;
        // 购物车图片
        .imgs-bg{
            width: 1.3rem;
            height: 1.3rem;
            // display: flex;
            align-items: center;
            justify-content: center;        
            background-color: #141d27;
            // border: 1px solid rebeccapurple;
            border-radius: 1rem;
            margin: -0.2rem 0.2rem 0.16rem 0.36rem;
            .count{
                position: absolute;
                left:1rem;
                width: .48rem;
                height: .32rem;
                border-radius: .3rem;
                line-height: .32rem;
                text-align: center;
                background: red;
            }
            .imgs{
                display: flex;
                align-items: center;
                justify-content: center;
                width: 1.0rem;
                height: 1.0rem;
                
                // line-height: 0.6rem;
                background: rgba(255,255,255,0.2);
                border-radius: 0.8rem;
                margin: 0.15rem auto;
                img{
                    width: 0.5rem;
                    height: 0.5rem;
                }
            }
        }    
        // 消费 
        .consume{
            color:rgba(255,255,255,0.4);
            // 
            height: 50%;
            margin-top: 0.28rem;
            margin-right: 0.24rem;
            border-right: 0.02rem solid rgba(255,255,255,0.1);
            p{
                width: 0.75rem;
                font-size: 0.32rem;
                margin-top: 0.1rem;
                margin-right: 0.3rem;
            }
        }     
        // 配送费   
        .dispatching{
            color:rgba(255,255,255,0.4);
            width: 3rem;
            height: 100%;
            text-align: left;
            p{
                display: inline-block;
                margin-top: 0.44rem;
                font-size: 0.24rem;
            }
        }   
        // 起送费  
        .Send{
            color:rgba(255,255,255,0.4);
            width: 2.1rem;
            height: 100%;
            text-align: center;
            background-color: #2b333b;
            p{
                margin-top: 0.45rem;
                font-size: 0.24rem;
            }
        }
    }
</style>



