<template>
<transition enter-active-class = 'animated fadeInDown' leave-active-class = 'animated fadeOutLeft'>
    <div class="details" v-show='flag'>
        <div class="bg">
            <div class="title">
                <p v-if='getSellerInfo' class="">{{getSellerInfo.name}}</p>
            </div>
            
            <!-- 星星组件 -->
            <div class="stars">
                <div class="shape">
                    <Star :score="getSellerInfo.score" :size="'large'"></Star>
                </div>
            </div>
            <!-- 优惠信息 -->
            <div class="discount_title">
                <hr style="height:2px;border:none;border-top:2px solid rgba(255,255,255,0.2);">
                <p>优惠信息</p>
                <div class="discount">
                    <div class="discount_item" v-for="(v,k) in getSellerInfo.supports" :key="k">
                        <img :src="v | imgChoose" alt="">
                        <span v-if='getSellerInfo'>{{v.description}}</span>
                    </div>
                    
                </div>
                <!-- 商家公告 -->
                <div class="seller_inform">
                    <hr style="height:2px;border:none;border-top:2px solid rgba(255,255,255,0.2);">
                    <p class="inform">商家公告</p>
                    <p v-if='getSellerInfo' class="bulletin">
                        {{getSellerInfo.bulletin}}
                    </p>
                </div>

                <!-- 关闭界面 -->
                <div class="bottom">
                    <p @click="noShow">×</p>
                </div>
            </div>
        </div>
    </div>
</transition>
</template>

<script>
import { mapGetters } from 'vuex'
import animate from 'animate.css'
import Star from '../star/star'
import imgChoose from '../pic.js'
export default {
    components:{
        Star
    },
    computed : {
        ...mapGetters([
            'flag',
            'getSellerInfo'
        ])
    },
    methods : {
        noShow () {
            this.$store.dispatch('noShowDetail')
        }
    }
}
</script>

<style lang="less" scoped>
    .details{
        position: fixed;
        left: 0;
		top: 0;
        width: 100%;
        height: 100%;
        
        // filter: blur(.1rem);
        background-color:rgba(7,17,27,0.8); 
        opacity: 0.9;
        z-index: 1;
        .bg{
            z-index: 100;
            width: 100%;
            height: 100%;
            position: fixed;
            left: 0;
            top: 0;
            padding: 1.28rem .72rem .64rem .72rem;
            box-sizing: border-box;
            .title{
                font-size: 0.32rem;
                color: rgb(255,255,255);
                font-weight: bold;
                line-height: 0.32rem;
                // border: 1px solid black;
                margin-bottom: .32rem;
                height: .32rem;
                p{
                    text-align: center;
                    margin: 0 auto;
                }
            }
            .stars{
                height: .48rem;
                width: 3.74rem;
                
                text-align: center;
                padding-top: .08rem; 
                box-sizing: border-box;
                display: flex;
                margin: 0 auto;
                margin-bottom: .52rem;
                .shape{
                    width: 3.5rem;
                    display: inline-block;
                    vertical-align: center;
                    padding: 0;
                    display: flex !important;
                    justify-content:space-between !important;
                    margin: 0 auto;
                    span{
                        margin-left: .2rem;
                        margin-right: .2rem;
                    }
                }
                img{
                    width: .32rem;
                    height: .32rem;
                    vertical-align: baseline;
                    margin-left: .2rem;
                    margin-right: .2rem;
                    flex:1;
                }

            }
            .discount_title{
                margin-bottom: .56rem;
                >p{
                    width: 1.6rem;
                    height: .27rem;
                    font-size: 0.27rem;
                    color: rgb(255,255,255);
                    font-weight: bold;
                    line-height: 0.27rem;
                    // border: 1px solid black;
                    padding: 0 .24rem;
                    box-sizing: border-box;
                    margin: 0 auto;
                    position: relative;
                    top:-.24rem;
                    background-color: #394149;
                    margin-bottom: .37rem;
                }
                .discount{
                    padding-left: .24rem;
                    box-sizing: border-box;
                    // border: 1px solid black;
                    margin-bottom: .76rem;
                    .discount_item{
                        height: .32rem;
                        // border: 1px solid black;
                        margin-bottom: .24rem;
                        img{
                            width: .32rem;
                            height: .32rem;
                            margin-right: .12rem;
                        }
                        span{
                            font-size: 0.24rem;
                            color: rgb(255,255,255);
                            font-weight: 100;
                            line-height: 0.32rem;
                            vertical-align: top;
                        }
                    }
                }
            }
            .seller_inform{
                .inform{
                    width: 1.6rem;
                    height: .27rem;
                    font-size: 0.27rem;
                    color: rgb(255,255,255);
                    font-weight: bold;
                    line-height: 0.27rem;
                    // border: 1px solid black;
                    padding: 0 .24rem;
                    box-sizing: border-box;
                    margin: 0 auto;
                    position: relative;
                    top:-.24rem;
                    background-color: #394149;
                    // margin-bottom: .48rem;
                }
                .bulletin{
                    padding: 0 .24rem;
                    font-size: 0.24rem;
                    color: rgb(255,255,255);
                    font-weight: 200;
                    line-height: 0.48rem;
                    margin-top: .41rem;
                }
            }
            .bottom{
                p{
                    font-size: 0.64rem;
                    color: rgba(255,255,255,0.5);
                    position: relative;
                    bottom: -1rem;
                    width: 100%;
                    height: 0.64rem;
                    margin: 0 auto;
                    clear: both;
                    text-align: center;
                    line-height: 0.64rem;
                    // stick footer
                }
            }
        }
    }
</style>





