<template>
    
  <div class="price">
        <div class="decrese" @click="reduce" v-show="price.count>0"> 
            <img src="../../../static/imgs/减 (1).png" > 
        </div>
        <div class="num" v-show="price.count>0" >{{price.count}}</div>
        <div class="add" @click="add">
            <img src="../../../static/imgs/加 (1).png" >
        </div>
  </div>
  
</template>

<script>
import Vue from 'vue'
export default {
    props:['price'],
    methods:{
        add(){
            if(!this.price.count){
                Vue.set(this.price,'count',1);
            }else{
                this.price.count ++;
            }
        },
        reduce(){
            if(this.price.count){
                this.price.count --;
            }
        }
    }

}
</script>

<style lang="less" scoped>
.price{
    width: 1.2rem;
	height: 0.5rem;
	display: flex;
    float: right;
    
    div{
        flex: 1;
        
    }
    .decrese{
        width: 0.42rem;
        height: 0.42rem;
        img{
            width: 0.42rem;
            height: 0.42rem;
            }
        }
    .add{
        width: 0.42rem;
        height: 0.42rem;
        line-height: 0.1rem;
        text-align: right;
        img{
            width: 0.42rem;
            height: 0.42rem;
            } 
        }
    .num{
        width: 0.42rem;
        height: 0.42rem;
        font-size: 0.2rem;
        color: rgb(147,153,159);
        line-height: 0.48rem;
        margin-left:0.14rem;
        margin-right:0.14rem;
        }
    }
</style>



